.Gnav {
	width: 100%;
	background-color: #000;
	margin-bottom: 30px;
}

.Gnav ul {
	display: -ms-flexbox;
	display: flex;
	margin: 0 auto;
	padding: 0;
	width: 980px;
	-webkit-box-sizing: border-box;
	        box-sizing: border-box;
	list-style: none;
	-ms-flex-align: center;
	    align-items: center;
}

.Gnav ul li {
	-ms-flex: 1 1;
	    flex: 1 1;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
	-ms-flex-align: center;
	    align-items: center;
	color: #fff;
	height: 50px;
	border-left: 1px #fff solid;
	border-right: 1px #fff solid;
	cursor: pointer;
	font-size: 30px;
}

.Gnav ul li.Active {
	background-color: rgba(255, 180, 0, 0.9);
	color: #000;
}

.Gnav ul li:hover {
	background-color: rgba(29, 184, 81, 0.8);
}



